/* Remove default styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth; }

a,
a:hover {
  text-decoration: none; }

input:focus,
button:focus,
input,
button {
  outline: none; }

ul {
  list-style: none; }

/* General Styles */
body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  color: #fff;
  background: linear-gradient(to right, #1ABC9C, #2C3E50);
  overflow-x: hidden; }

.container {
  width: 100%;
  max-width: 1580px;
  padding: 0 15px;
  margin: 0 auto !important; }

/* ////////////////////////////////
Header Styles START
//////////////////////////////// */
.header {
